import { Injectable } from '@angular/core';
import { Teacher } from '../model/teacher';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Course } from '../model/course';

@Injectable({
  providedIn: 'root'
})
export class TeacherService {

  private teachersUrl: string;
  private coursesUrl: string;
  private baseURl:string;
  private coursesURl:string;
  private testURl:string;
  private firstURL:string;
  private cours :string;

  constructor(private http: HttpClient) {
    this.teachersUrl = 'http://localhost:8080/teachers';

    this.coursesUrl = 'courses';
    this.baseURl="http://localhost:8080/";
    this.testURl="http://localhost:8080/getAllCourses";
    this.firstURL="http://localhost:8080/first";
    this.cours='course'

  }
  public findAll(): Observable<Teacher[]> {
  
    return this.http.get<Teacher[]>(this.teachersUrl);
}
  public findCoursesOfTeacher(id :number): Observable<Course[]>{
    const url = `${this.baseURl}/${id}/${this.coursesUrl}`;
    return this.http.get<Course[]>(url);
  }
  public findAllCourses():Observable<Course[]>{
   return this.http.get<Course[]>(this.testURl);
  }

  public findTeacherByFirstName(name:string):Observable<Teacher[]>{
    const url =`${this.firstURL}/${name}`;
    return this.http.get<Teacher[]>(url);
  }

  public CreateACourse(id:number,cou:Course){
    const url =`${this.baseURl}/${id}/${this.cours}`;
    return this.http.post<Course>(url,cou);
  }

}
