import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminManagementComponent } from './admin-management/admin-management.component';
import { CreatingCourseComponent } from './creating-course/creating-course.component';



const routes: Routes = [{ path: 'AdminManagement', component: AdminManagementComponent
 },
 { path: 'CreateCourse', component: CreatingCourseComponent
 }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }