import { Component, OnInit } from '@angular/core';
import { Course } from '../model/course';
import { TeacherService } from '../service/teacher.service';
import { Teacher } from '../model/teacher';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-creating-course',
  templateUrl: './creating-course.component.html',
  styleUrls: ['./creating-course.component.css']
})
export class CreatingCourseComponent implements OnInit {
  teachers: Teacher[]=new Array();
  teachers2: Teacher[]=new Array();
  course : Course;
  selectedname:string;
  teaa :Teacher;
  firstNames: string[]=[];
  courseChoosen:string;
  NotYet=false;
  Yet=true;

  constructor(private teacherService:TeacherService,private route: ActivatedRoute, private router: Router) { 
    this.course=new Course();
    
  }

  ngOnInit() {
    this.teacherService.findAll().subscribe(data => {
      this.teachers = data;
    });

   
  }
  
  Action(cityindex){
    for(var i=0;i<this.teachers.length;i++){
      this.firstNames[i]=this.teachers[i].firstName;
    }
    console.log(this.firstNames[cityindex]);
    this.teacherService.findTeacherByFirstName(this.firstNames[cityindex]).subscribe(data => {
      this.teachers2 = data;
      this.End();
    
    });  
    

  }
  End(){
    console.log(this.teachers2[0].city);
    this.course.courseName=this.courseChoosen;
    this.teacherService.CreateACourse(this.teachers2[0].id,this.course).subscribe(result => this.gotoClientList());
    

  }
  gotoClientList() {
    console.log("ok");
    this.router.navigate(['/AdminManagement']);
  }
  onSubmit(){
    this.Yet=false;
    this.NotYet=true;
    
  }
  
 
}
